.color-picker {
    text-align: center;
    
  }
  
  .color-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 150px;
    
  }
  
  .color-option {
    width: 40px;
    height: 40px;
    margin: 5px;
    cursor: pointer;
  }
  
  button {
    background-color: #0074d9;
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 10px;
    cursor: pointer;
    margin-top: 20px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .box{
    background-color: rgb(233, 140, 156);
    box-shadow:2px 2px 10px 10px #1e1a22;
    border-radius: 10px;
    height:500px;
    width: 900px;
    margin-left: 250px;
  }
body{
    background-color: rgb(175, 136, 175);
}
